<template>
	<div id="single-blog">
		<div class="tit" v-rainbow>{{blog.title}}</div>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<p>分类：</p>
		<ul>
			<li v-for="item in blog.category">{{item}}</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name:'single-blog',
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://wd2206394391jwoklu.wilddogio.com/Aposts/'+this.id+'.json').then((res)=> {
				console.log(888,res.body)
				this.blog = res.body
			})
		}
	}
</script>

<style scoped>
	#single-blog{
		box-sizing: border-box;
		max-width: 580px;
		margin: 0 auto;
		background: #ddd;
		padding: 10px;
	}
	article{
		background: #eee;
		text-indent: 20px;
		padding: 10px;
	}
	.tit{
		background: #ddd;
		font-size: 24px;
		padding:10px 0 20px;
		/* font-weight: bold; */
	}
</style>


