<template>
  <div id="add-blog">

    
    <form action="" v-if="submitie">
      <div>
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required=""/>
      </div>     
      <div>
        <label for="">博客内容</label></br>
        <textarea rows="5" v-model="blog.content"></textarea>
      </div>
      
      <div id="checkboxes">
        <label for="">Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.category">
        <label for="">Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.category">
        <label for="">React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.category">
        <label for="">Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.category">
      </div>

      <label for="">作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>
      </br>
      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="!submitie">
      <h3>您的博客发布成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="item in blog.category">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog: {
        title:'',
        content:'',
        category:[],
        author:"请选择"
      },
      authors:['请选择','helen','chric','alice'],
      submitie: true
    }
  },
  methods:{
    post: function(){
      this.$http.post("https://wd2206394391jwoklu.wilddogio.com/Aposts.json",this.blog).then(function(data){
          console.log(data)
          this.submitie = false
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
  margin: 5px 0;
  padding: 10px;
  background: #ddd;
}
#add-blog{
  margin: 0 auto;
  max-width: 580px;

}
/* label{
  display: block;
  margin:  20px 0 10px;
} */
input[type="text"], textarea{
  width: 100%;
  padding: 0;
  margin: 0;
/*   list-style: none; */
  /* outline: none; */
  border: 1px solid #ccc;

}

</style>



