<template>
	<div class="top">
		<div class="tit">My Blogs</div>		
		<nav>
			<div>
				<router-link to="/" exact>博客总览</router-link>
				<router-link to="/add" exact>添加博客</router-link>	
			</div>				
					
		</nav>
	</div>	
</template>

<script>
	export default {
		name: 'blog-header'
	}
</script>

<style scoped>
	nav{		
		background: #ddd;
		padding: 0 10px ;	
		margin: 5px auto;	
		display: flex;
		justify-content: space-between;
	}	
	a{
		display: inline-block;
		width: 80px;
		color: #444;
		text-decoration: none;
		padding: 6px;
		/* border-radius: 5px; */
		text-align: center;
		background: #ddd;
	}
	.router-link-active{
	
		color: #fff;
	}
	.top{
		max-width: 580px;
		margin: 0 auto;
	}
	.tit{
		background: #ddd;
		font-size: 26px;
		padding: 20px;
		/* font-weight: bold; */
	}
	

		
</style>


